import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	computed: mapState(['todoList', 'userName', 'personid', 'header']),
	data() {
		return {
			"list": []
		}
	},
	methods: {

		...mapMutations(['setTodo']),

		cilck(item) {
			/* 	if (item.id < 5000) {
					uni.showToast({
						title: "暂未开放该应用",
						icon: "none"
					})

					return
				} */
			//uni.hideToast();
			uni.navigateTo({
				url: "../defect/defect?id=" + item.OWNERID.toString()
			})
		}
	},
	onHide() {

	},
	onShow() {
		console.log("onShow")
		if (this.personid == null || this.personid == '') {
			return
		}
		this.list = this.todoList;
	},
	onLoad() {
		console.log("进待办了")
		if (this.personid == null || this.personid == '') {
			return
		}
		this.list = this.todoList;
		//访问接口
		/* uni.showLoading({
			title:"加载中..."
		})
		uni.request({
			url: "https://unidemo.dcloud.net.cn/api/news",
			method: "GET",
			success: res =>{
				console.log(res)
			},
			complete() {
				uni.hideLoading()
			}
		}) */
	},
	onNavigationBarButtonTap(btn) {
		console.log(btn.index)
		this.list.reverse();
	},
	onPullDownRefresh() {
		if (this.personid == null || this.personid == '') {
			uni.stopPullDownRefresh();
			return
		}
		this.$http.post("/mem/workflow/getdb", {
			"PERSONID": this.personid
		}, ).then(res => {

			if (res.data.META.SUCCESS) {
				this.setTodo(res.data.DATA);
				uni.stopPullDownRefresh();

				uni.setTabBarBadge({
					index: 1,
					text: res.data.DATA.length.toString()
				});

			}
		}).catch(err => {
			uni.stopPullDownRefresh();
			uni.showToast({
				title: JSON.stringify(err),
				icon: "none"
			})
		})

	}
}
